import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CustomCenterHttpService } from 'app/pages/tzb/customCenter/http/custom-center.http.service';
import { QueryCustBean } from './bean/query-cust-post.bean';

@Component({
    selector: "query-cust-post1",
    templateUrl: './query-cust-post1.component.html',
    styleUrls: ['./query-cust-post1.component.scss'],
    providers: [CustomCenterHttpService]
})
export class QueryCustPost1 implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;
    queryCustBean: QueryCustBean = new QueryCustBean();
    pageSize: any;
    total: string;
    first: number = 0;
    tableData: any[];
    //提示信息
    msgs: Message[] = [];
    constructor(
        public httpService: CustomCenterHttpService,

    ) {

    }
    ngOnInit() {
        if (this.inValue) {
            this.queryCustBean.partyId = this.inValue;
        }
        this.queryCustBean.pageNum = '1';
        this.queryCustBean.pageSize = '10';
        this.queryScale();
    }

    queryScale() {
        this.queryCustBean.pageNum = '1';
        this.queryCustBean.pageSize = '10';
        this.first = 0;
        this.quertList();
    }

    quertList() {
        this.queryCustBean.flag = '0';
        this.queryCustBean.subFlag = '0';
        this.httpService.queryOrgPostTellerRelation(this.queryCustBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgPostTellerList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    // 分页
    paginate(event) {
        this.queryCustBean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.queryCustBean.pageNum = currentPage.toString();
        this.first = parseInt(this.queryCustBean.pageSize) * parseInt(this.queryCustBean.pageSize);
        this.quertList();
    }

    // 选中一行
    onRowSelect(event) {
        let personArr = [];
        personArr.push(event.data)
        this.outValue.emit(personArr);
    }

    // 重置
    reset() {
        this.queryCustBean.pageNum = '1';
        this.queryCustBean.pageSize = '10';
        this.first = 0;
        this.queryCustBean.postName = undefined;
        this.queryCustBean.postId = undefined;
        this.queryCustBean.userLoginId = undefined;
        this.queryCustBean.userName = undefined;
    }
}