import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService, TreeNode } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { UisftechCommonHttpService } from 'app/@uisftech/common/http/uisftech-common-http.service';
import { OrgTreeBean } from './bean/query-cust-org.bean';

@Component({
    selector: 'query-cust-org1',
    templateUrl: './query-cust-org1.component.html',
    styleUrls: ['./query-cust-org1.component.scss'],
    providers: [UisftechCommonHttpService, Commfunc]
})

/**
 * 机构对应关系
 */
export class QueryCustOrgTree1 implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();


    orgEle: any;
    orgTreeBean: OrgTreeBean = new OrgTreeBean();
    treeOrgName: string;
    //初始机构号
    org: string;
    //提示信息
    msgs: Message[] = [];
    date: any;
    treeList2: string;
    selectedFiles;

    constructor(
        public uisftechCommonHttpService: UisftechCommonHttpService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit() {
        if (this.inValue) {
            this.org = this.inValue;
            this.query()
        } else {
            this.org = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
            this.query();
        }
    }
    //选中
    nodeCheck(event) {
        this.treeOrgName = event.node.label;
        this.orgEle = { orgId: event.node.orgId, orgName: event.node.orgName, location: event.node.location }
        this.outValue.emit(this.orgEle);
    }

    //点击加号
    nodeExpend(event) {
        this.org = event.node.orgId;
        this.orgTreeBean.orgId = this.org;
        this.orgTreeBean.relationshipType = '-1';
        if (event.node.children && event.node.children.length > 0) {
            return;
        }
        this.uisftechCommonHttpService.queryOrgByOrgIdAndRelationship(this.orgTreeBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                event.node.children = this.toTree(data.orgList);
            }
        })
    }

    toTree(data) {
        if (data) {
            data.forEach(item => {
                item['label'] = item['orgName']
                item['orgId'] = item['orgId']
                item['location'] = item['location']
            });
            return data
        }

    }
    //查询
    query() {
        this.orgTreeBean.orgId = this.org;
        this.orgTreeBean.relationshipType = '0';
        this.uisftechCommonHttpService.queryOrgByOrgIdAndRelationship(this.orgTreeBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.date = this.toTree(data.orgList);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'info', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

}